.food-strategy {
  margin-bottom: 20px;
  background-color: @basic-grey;
}

.food-strategy__title {
  margin: 0 0 23px;
  padding: 24px 0 0 20px;
  display: flex;

  .font-oswald-regular(1.5em);
  line-height: 2em;
  text-transform: uppercase;

  &::before {
    content: "";
    display: block;

    width: 50px;
    height: 50px;
    margin-right: 20px;

    background-color: @green;
    border-radius: 50%;
  }
}

.food-strategy__text {
  padding: 0 20px;
  margin-bottom: 20px;

  font-size: 0.87em;
  line-height: 1.28em;
}
